export default function ({ $axios }, inject) {
    let apiKey = '37aedad54b19c6dbb46edXNlcnRlc3Qx';

    if (process.client) {
        let user = localStorage.getItem('user_info') ? JSON.parse(localStorage.getItem('user_info')) : null;
        if (user && user.api_key) {
            apiKey = user.api_key
        }
    }
    // Create a custom axios instance
    const http = $axios.create({
        headers: {
            'api-key': apiKey,
        }
    })
  
    // Set baseURL to something different
    http.setBaseURL(process.env.NUXT_ENV_1989S_MEDIA)
  
    // Inject to context as $api
    inject('http', http)
}