export const state = () => ({
    histories: []
})
  
export const getters = {
    histories(state) {
        return state.histories
    }
}

export const mutations = {
    setHistory(state, histories) {
        return state.histories = histories
    }
}

export const actions = {
    async histories({ commit }) {
        let response = await this.$http.get(`/v2/log?action=${this.action}&key=${this.key}&limit=${this.limit}`)
        commit('setHistory', response.data.data);
    }
}