export const state = () => ({
    user: []
})
  
export const getters = {
    getUser(state) {
        return state.user
    }
}

export const mutations = {
    changeUser(state, user) {
        return state.user = user
    }
}

export const actions = {
    async changeUser(context, user) {
        context.commit('changeUser', user)
    }
}