import config from './../config/config'

import Header from '../components/layouts/services/Header.vue';
import Menu from '../components/layouts/services/Menu.vue';
import Content from '../components/layouts/services/Content.vue';
import Footer from '../components/layouts/services/Footer.vue';
import { writeLog } from '../helper/helper';
import GoogleTagManager from '../components/common/GoogleTagManager.vue';

export default {
    data() {
        return {
            user: null,
        }
    },
    head: {
        title: config.title,
            htmlAttrs: {
            lang: 'en'
        },
        meta: [
            { charset: 'utf-8' },
            { name: 'viewport', content: 'width=device-width,initial-scale=1.0,user-scalable=0,minimal-ui' },
            { hid: 'description', name: 'description', content: config.description },
            { hid: 'og:description', name: 'og:description', content: config.description },
            { hid: 'keywords', name: 'keywords', content: config.keyword },
            { hid: 'dc.keywords', name: 'dc.keywords', content: config.keyword },
            { hid: 'news_keywords', name: 'news_keywords', content: config.keyword },
            { hid: 'og:site_name', name: 'og:site_name', content: config.siteName2 },
            { hid: 'dc.title', name: 'dc.title', content: config.title },
            { hid: 'og:title', name: 'og:title', content: config.title },
            { hid: 'og:image', name: 'og:image', content: config.ogImage },
            { name: 'format-detection', content: 'telephone=no' },
        ],
        link: [
            { rel: 'icon', type: 'image/x-icon', href: 'https://i.imgur.com/SFFQPrk.png' },
            { rel: 'apple-touch-icon', type: 'image/x-icon', href: 'https://i.imgur.com/SFFQPrk.png' },
            { rel: 'stylesheet', type: "text/css", href: '/app-assets/vendors/css/vendors.min.css'},
            { rel: 'stylesheet', type: "text/css", href: '/app-assets/css/bootstrap.css'},
            { rel: 'stylesheet', type: "text/css", href: '/app-assets/css/bootstrap-extended.css'},
            { rel: 'stylesheet', type: "text/css", href: '/app-assets/css/colors.css'},
            { rel: 'stylesheet', type: "text/css", href: '/app-assets/css/components.css'},
            { rel: 'stylesheet', type: "text/css", href: '/app-assets/css/themes/dark-layout.css'},
            { rel: 'stylesheet', type: "text/css", href: '/app-assets/css/themes/bordered-layout.css'},
            { rel: 'stylesheet', type: "text/css", href: '/app-assets/css/themes/semi-dark-layout.css'},
            { rel: 'stylesheet', type: "text/css", href: '/app-assets/css/core/menu/menu-types/vertical-menu.css'},
            { rel: 'stylesheet', type: "text/css", href: '/app-assets/vendors/css/extensions/sweetalert2.min.css'},
            { rel: 'stylesheet', type: "text/css", href: '/fontawesome-pro/css/fontawesome.css'},
            { rel: 'stylesheet', type: "text/css", href: '/fontawesome-pro/css/brands.css'},
            { rel: 'stylesheet', type: "text/css", href: '/fontawesome-pro/css/solid.css'},
            { rel: 'stylesheet', type: "text/css", href: '/fontawesome-pro/css/light.css'},
            { rel: 'stylesheet', type: "text/css", href: '/fontawesome-pro/css/regular.css'},
            { rel: 'stylesheet', type: "text/css", href: '/fontawesome-pro/css/thin.css'},
            { rel: 'stylesheet', type: "text/css", href: '/fontawesome-pro/css/v4-shims.css'},
            // { rel: 'stylesheet', type: "text/css", href: '/app-assets/vendors/css/tables/datatable/dataTables.bootstrap4.min.css'},
        ],
        script: [
            { src: '/app-assets/vendors/js/vendors.min.js', body: true },
            { src: '/app-assets/vendors/js/extensions/sweetalert2.all.min.js', body: true },
            // { src: '/app-assets/vendors/js/tables/datatable/jquery.dataTables.min.js', body: true },
            // { src: '/app-assets/vendors/js/tables/datatable/datatables.bootstrap4.min.js', body: true },
            { src: '/app-assets/js/core/app-menu.js', body: true },
            { src: '/app-assets/js/core/app.js', body: true },
        ],
        bodyAttrs: {
            class: 'vertical-layout vertical-menu-modern  navbar-floating footer-static',
            'data-open': 'click',
            'data-menu': 'vertical-menu-modern',
            'data-col': '',
        }
    },
    components: {
        Header,
        Menu,
        Content,
        Footer,
        GoogleTagManager,
    },
    methods: {
        async userInfo() {
            try {
                let response = await this.$http.get('/v2/me');
                if (response?.data?.data) {
                    this.$store.commit('changeUser', response.data.data)
                    this.user = response.data.data;
                }
            } catch (error) {
                this.$notify.error({
                    title: 'Error',
                    message: error.response.data.message
                });
            }
        },
    },
    mounted() {
        if (process.client) {
            let user = localStorage.getItem('user_info') ? JSON.parse(localStorage.getItem('user_info')) : null;
            if (!user || user.length <= 0) {
                // this.$router.push( { path: '/login' } )
            } else {
                this.$store.commit('changeUser', user)
            } 
        }
    },
    created() {
        this.userInfo()
        writeLog(this.$route.path);
    },
}
