import config from './../config/config'
import { setServiceGlobal } from '../plugins/globalVariable'
const services = config.menu
export function getService(route) {
    let serviceKey = route.path.split('/')
    let serviceName = capitalizeFirstLetter(serviceKey[1]) != "" ? capitalizeFirstLetter(serviceKey[1]) : "Facebook"
    let results = services[serviceName]
    let service = results.find(x => x.url == route.path)
    setServiceGlobal(service)
    return service
}

export function capitalizeFirstLetter(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
}
  
export function createPriceList(priceList, length = 11) {
    let prices = [];
    for (let i = 1; i < length; i++) {
        let price = {
            number: priceList.number * i,
            price: priceList.price * i
        }
        prices.push(price)
    }
    return prices;
}

export const writeLog = async (end_point) => {
    let data = {
        end_point,
        // debug: 1
    }
    let response = await fetch(config.logging.api_create, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify(data) 
    })

    return response.json();
}